$body-color: #fff;
$bg-color:#11131b;
$bg-color-light: #1a1d28;
$color-green: #238636;
$font-color:#6a748e;
$color-red:#e9576f;
.discord-list-container {
    max-height: 400px;
    background-color: $bg-color-light;
    // max-width: 400px;
    margin: 20px auto;
    overflow-y: scroll;
    border-radius: 5px;
    overflow-x: visible;
    .loading {
        display: none;
    }
    &.-loading {
        .loading {
            display: block;
        }
    }
  }
.discord-list {
    &>li {
        
    }
    // @media only screen and (max-width: 400px) {
    //     max-width: 100%;
    // }
    list-style: none;
    padding: 0;
    margin: 0;
  
    .d-content {
        padding-left: 10px;
        width: 100%;
    }
    .d-author {
        img {
            width: 40px;
        border-radius: 50%;
        }
    }
    li {
      padding: 10px;
      border-bottom: $bg-color 1px solid;
      overflow-wrap: anywhere;
    //   font-size: 10px;
      display: flex;
    }
  }

.content.-discord {
    .section>h2 {
        margin-bottom: 0px;
        font-weight: 400;
    }
}

.d-content {
    font-weight: 300;
    font-size: 14px;
    max-width: 100%;
    img.d-emoji {
        height: 14px;
    }

    @media only screen and (max-width: 600px) {
        font-size: 12px;
        img.d-emoji {
            height: 12px;
        }
      }
    a {
        color: rgb(0, 175, 244);
    }
    .author {
        color: rgb(233, 30, 99);
        font-weight: 500;
        margin:0;
    }
    .time-posted {
        color: $font-color;
        font-weight: 300;
        font-size: 12px;
    }
    .reactions {
        display: flex;
        flex-wrap: wrap;
       margin-top: 10px;
    }
    .reaction {
        cursor: not-allowed;
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        padding: .125rem .375rem;

        background-color: $bg-color;
        border-radius: .5rem;
        margin-right: .25rem;
        margin-bottom: .25rem;
        color:$font-color;
        font-weight: 400;

   
        
    }
    .reaction > .emoji {
        width: 1rem;
        height: 1rem;
        margin: .125rem 0;
        min-width: auto;
        min-height: auto;
        margin-right: 5px;
    }
}

.discord-embed-article {
    border-radius: 5px;
    border-left: 5px solid #11131b;
    background: #181a24;
    padding: 5px 10px;
    margin-top: 10px;
    h3 {
        margin: 10px 0px 5px;
    }
    .a-title {
        width: 100%;
        display: block;
    }
    .a-img {
        width: 100%;
    }
}
