$body-color: #fff;
$bg-color:#11131b;
$bg-color-light: #1a1d28;
$color-green: #238636;
$font-color:#6a748e;
$color-red:#e9576f;
.server-status {
    margin: 0px auto;
    max-width: 500px;
    text-align: center;
    span.serv {
        margin: 0px 5px;
    }
    h4 {
        margin-bottom: 5px;
    }
    .desc {
        margin: 10px;
    }
    .servers {
        padding: 0px 15px;
    }
}
.serv {
    font-size: 12px;
    padding:5px 10px;
    background-color: #1a1d28;
    border-radius: 5px;
    color: white;
    text-decoration: none;
    display: inline-block;
    margin:0px 5px 5px 0px;
    
    &:hover {
        outline:1px solid white;
    }
    .fas {
        margin-right: 5px;
    }
    .fas {
        color: $color-green;
    }
}

.serv.-down {
    .fas {
        color: $color-red;
    }
}
.fpvu-rate {
    text-align: center;
}
.fpvu-fusd-rate {
    display: inline-block;
    text-decoration: none;
    color: white;
    font-weight: 500;
    font-size: 14px;
    cursor: pointer;
    background-color: rgb(93, 95, 239);
    width: fit-content;
    padding: 8px 12px;
    border-radius: 10px;
    margin:5px 5px;
    cursor: pointer;
    &.display-none {
        display: none;
    }

    &:hover {
        outline: 1px solid white;
    }
}