.plant-container {
    max-width: 290px;
    height: 260px;
    margin: 10px auto;
    background-color: #161922;
    border: 1px solid rgb(17, 24, 39);
    border-radius: 10px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    .seed-rarity {
        width: 100px;
        height: 25px;
        position: relative;
        margin-bottom: 5px;
        img {
            width: 100%;
        }

        .seed-rarity-text {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            text-align: center;
            font-size: 12px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
    .top-stat-right {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        font-size: 16px;
        line-height: 20px;

        position: absolute;
        right: 10px;
        .seed-stat {
            display: flex;
            align-items: baseline;
        }
        .unit {
            color:#6a748e;
            margin-right: 3px;
            font-size: 10px;
        }
    }
    .plant-img-container { 
        display: flex;
        justify-content: center;
        align-items: center;
        
        img {
            height: 140px;
            width: auto;
        }
    }

   

    .le-hours-rate {
        border: .5px solid #fff;
          border-radius: 2px;
          font-size: 12px;
          line-height: 21px;
          color: #fff;
          padding: 1px 6px;
          margin: 0;
          display: inline;
      }
}

.plant-container:hover {
    border: white 1px solid;
    background-color: #1a1d28;
}

.plant-stat-container {
    display: flex;
    justify-content: space-between;
}

.seed-checker {
    #seed-rarity-type-text {
        display: none;
    }
    .show-id-btn-container {
        display: none;
    }

    #seedId {
        padding: 5px 30px;
    }
}
.pid-v-icon {
    margin-left: -33px;
    font-size: 13px;
    color: #6a748e;
   i {
        padding: 4px 6px;
   }
}
.seed-checker.-hide-plant-id {
    .hide-id-btn-container {
        display: none;
    }
    .show-id-btn-container {
        display: inline-block;
    }

    #seed-rarity-type-text {
        display: flex;
    }

    #seed-rarity-text {
        display: none;
    }
    .seed-currency {
        display: none;
    }
    .-calculate-price {
        display: none;
    }
    .weather-effect-title {
        display: none;
    }
}

.sc-class {
    display: block;
}

.sc-stats {
