    // margin-top: 30px;
}
.stat {
    display: flex;
    margin-top: 3px;
    .stat-icon {
        background: #292d3e;
        border-radius: 2px;
        padding: 4px;
        margin-right: 4px;
        width: 12px;
    }

    .stat-icon.mt{
        background: #292d3e;
        border-radius: 2px;
        padding: 0px;
        margin-right: 4px;
        width: 18px;
        height: 18px;
    }
    .stat-value {

    }
}

.seed-stat.pve-stats {
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
}