.ads-desktop {
    text-align: center;
    @media only screen and (max-width: 720px) {
        display: none;
    }
}

.ads-mobile {
    text-align: center;
    @media only screen and (min-width: 720px) {
        display: none;
    }
}

.ads-section {
    text-align: center;
    margin-bottom: 20px;
}

.-with-side-ads {
    display: flex;
    flex-direction: row;
    .ads-side-section {
        flex-grow: 1;
        text-align: center;
        @media only screen and (max-width: 930px) {
            display: none;
        }
    }

    
    .main-content {
        flex-grow: 1;
        max-width: 600px;
        @media only screen and (max-width: 930px) {
            max-width: initial;
        }
    }

    .mock-ads {
        background:white;
        width: 160px;
        height: 600px;
    }
}